<template>
  <!--  -->
  <v-dialog
    v-model="show"
    persistent
    max-width="290"
  >
    <v-card>
      <v-card-title/>
      <v-card-text class="black--text">
        
      </v-card-text>
      <v-card-actions>
        <v-spacer/>
        <v-btn color="grey" text :disabled="loading" @click="onClickClose"></v-btn>
        <v-btn color="red" text :loading="loading" @click="onClickDelete"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapActions, mapState } from 'vuex'


export default {
  name: 'DeleteDialog',

  
  data () {
    return {
      /**  */
      show: false,
      /**  */
      item: {}
    }
  },
  computed: mapState({
    /**  */
    loading: state => state.loading.delete
  }),
  methods: {
    ...mapActions([
      /**  */
      'deleteAbData'
    ]),
    /**
     * 
     * 
     */    
    open (item) {
      this.show = true
      this.item = item
    },
    /**  */
    onClickClose () {
      this.show = false
    },
    /**  */
    async onClickDelete () {
      await this.deleteAbData({ item: this.item })
      this.$router.go({path: this.$router.currentRoute.path, force: true})
      this.show = false
    }
  }
}
</script>