<template>
  <div class="form-wrapper">
    <p></p>
    <v-form v-model="valid">
      <h3></h3>
      <!--  -->
      <v-text-field
        label=""
        v-model="settings.appName"
        :counter="30"
        :rules="[appNameRule]"
      />
      <!-- API URL -->
      <v-text-field
        label="API URL"
        v-model="settings.apiUrl"
        :counter="150"
        :rules="[stringRule]"
      />
      <!-- Auth Token -->
      <v-text-field
        label="Auth Token"
        v-model="settings.authToken"
        :counter="150"
        :rules="[stringRule]"
      />
      <h3></h3>
      <p> &#44; </p>
      <!--  -->
      <v-text-field
        label=""
        v-model="settings.strIncomeItems"
        :counter="150"
        :rules="[stringRule, ...categoryRules]"
      />
      <!--  -->
      <v-text-field
        label=""
        v-model="settings.strOutgoItems"
        :counter="150"
        :rules="[stringRule, ...categoryRules]"
      />
      <!--  -->
      <v-text-field
        label=""
        v-model="settings.strTagItems"
        :counter="150"
        :rules="[stringRule, tagRule]"
      />
      <v-row class="mt-4">
        <v-spacer/>
        <v-btn color="primary" :disabled="!valid" @click="onClickSave"></v-btn>
      </v-row>
    </v-form>
  </div>
</template>

<script>
export default {
  name: 'Settings',

  data () {
    const createItems = v => v.split(',').map(v => v.trim()).filter(v => v.length !== 0)
    const itemMaxLength = v => createItems(v).reduce((a, c) => Math.max(a, c.length), 0)

    return {
      /**  */
      valid: false,
      /**  */
      settings: { ...this.$store.state.settings },
        

      /**  */
      appNameRule: v => v.length <= 30 || '30',
      stringRule: v => v.length <= 150 || '150',
      categoryRules: [
        v => createItems(v).length !== 0 || '1',
        v => itemMaxLength(v) <= 10 || '10'
      ],
      tagRule: v => itemMaxLength(v) <= 10 || '10'
    }
  },

  methods: {
   /**  */
    onClickSave () {
      this.$store.dispatch('saveSettings', { settings: this.settings })
    }
  }
}
</script>

<style>
.form-wrapper {
  max-width: 500px;
  margin: auto;
}
</style>